import { Button, Form, Modal, Select } from "antd";
import {
  CANCEL_TEXT,
  CONFIRM_MESSAGE,
  EMP_STATUS,
  FORM_LAYOUT,
  SUBMIT_TEXT,
  UPDATE_TEXT,
} from "constants/CommonConstants";
import { EMP_STATUS_FORM_NAME } from "constants/ManageProjects";

const EmployeeStatus = ({ visible, onCancel, empData, engageResourceHandler }) => {
  const [form] = Form.useForm();
  const { Option } = Select;
  const { confirm } = Modal;

  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onCancel();
      },
    });
  };

  const handleCancel = () => {
    if (form.isFieldsTouched()) {
      handleConfirm();
    } else {
      onCancel();
    }
  };

  const handleOk = () => {
    form
      .validateFields()
      .then(values => {
        form.resetFields();
        engageResourceHandler(values);
        onCancel();
      })
      .catch(info => {
        console.log(info);
      });
  };

  const empStatus = ["Billable", "Non-Billable", "Shadow", "Trainee"];

  return (
    <Modal
      visible={visible}
      onCancel={handleCancel}
      onOk={handleOk}
      centered
      footer={[
        <Button key={CANCEL_TEXT.value} onClick={handleCancel}>
          {CANCEL_TEXT.text}
        </Button>,
        <Button key={SUBMIT_TEXT.value} onClick={handleOk} type={"primary"}>
          {UPDATE_TEXT.text}
        </Button>,
      ]}
    >
      <Form form={form} layout={FORM_LAYOUT.horizontal} name={EMP_STATUS_FORM_NAME} className="mt-4">
        <Form.Item
          name={EMP_STATUS.name}
          label={<span className="text-wrap">{EMP_STATUS.label}</span>}
          rules={[{ required: true, message: EMP_STATUS.invalid_message }]}
        >
          <Select placeholder={EMP_STATUS.placeholder} allowClear>
            {empStatus.map(e => {
              return (
                <Option key={e} value={e}>
                  {e[0].toUpperCase() + e.slice(1)}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default EmployeeStatus;
